import React, { useEffect, useState, useRef } from "react";
import { formatNumber } from "../../services/utilities";
import _ from "lodash";
import Nav from "../common/nav";
import Footer from "../common/footer";

import CircleGraph from "../common/circleGraph";
import Button from "../common/Button";
import Input from "../common/form-input";
import AdjustableInput from "../common/form-input-adjustable";
import Textarea from "../common/form-textarea";
import Select from "../common/form-select";
import SelectColor from "../common/form-select-color";
import Table from "../common/table";

const Envelopes = ({ id }) => {
  const ScrollRef = useRef(null);
  const [envelope, setEnvelope] = useState({});
  const [envelopeCopy, setEnvelopeCopy] = useState({});
  const [envelopes, setEnvelopes] = useState([
    {
      id: 1,
      name: "Emergency Savings",
      account: "HY Savings",
      desc: "Buffer fund",
      tag: { name: "Savings", color: "warning" },
      values: {
        goal: 12687,
        amount: 10724.73,
        percentage: 85,
      },
    },
    {
      id: 2,
      name: "Dining Out",
      account: "General Spending",
      desc: "Money for eating out",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 120,
      },
      history: [
        { date: "08/16/2021", amount: "-20.25", source: "Credit" },
        { date: "08/14/2021", amount: "-10.65", source: "Bank" },
        { date: "08/11/2021", amount: "30.50", source: "Credit" },
      ],
    },
    {
      id: 3,
      name: "Groceries",
      account: "General Spending",
      desc: "Money for grocery spending",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 280.19,
      },
    },
    {
      id: 4,
      name: "Kids",
      account: "General Spending",
      desc: "Kid related purchases",
      tag: { name: "Kids", color: "info" },
      values: {
        goal: null,
        amount: 30,
      },
    },
  ]);
  const [tagColors, setTagColors] = useState([
    { name: "blue", value: "primary" },
    { name: "grey", value: "secondary" },
    { name: "green", value: "success" },
    { name: "red", value: "danger" },
    { name: "yellow", value: "warning" },
    { name: "teal", value: "info" },
    { name: "black", value: "dark" },
  ]);

  const [editable, toggleEditable] = useState(true); // disabled true
  const [showPosAdjuster, togglePosAdjuster] = useState(false);
  const [showNegAdjuster, toggleNegAdjuster] = useState(false);
  const [addTotal, setAddTotal] = useState("");
  const [subTotal, setSubTotal] = useState("");
  const [source, setSource] = useState("Credit");

  useEffect(() => {
    if (id) {
      setEnvelope(envelopes.find((value) => value.id === Number(id)));
    }
  }, [id, envelopes]);

  const adjuster = (type = null) => {
    togglePosAdjuster(false);
    toggleNegAdjuster(false);
    setAddTotal("");
    setSubTotal("");
    if (type !== null) {
      type ? togglePosAdjuster(true) : toggleNegAdjuster(true);
      setTimeout(() => scrollToRef(), 100);
    }
  };

  // Format value to 2 decimals
  const formatValues = (num, type) => {
    if (type === "+") {
      num = formatNumber(num, addTotal);
      num && setAddTotal(num);
    }
    if (type === "-") {
      num = formatNumber(num, subTotal);
      num && setSubTotal(num);
    }
  };

  const add2total = () => {
    let update = envelope;

    // Add up values and set to 2 decimals
    update.values.amount = (
      Number(update.values.amount) + Number(addTotal)
    ).toFixed(2);

    if (update.values.goal) {
      update.values.percentage = Math.round(
        (update.values.amount / update.values.goal) * 100
      );
    }
    updateHistory(update, addTotal);
  };

  const subFromTotal = () => {
    let update = envelope;
    // Subtract values and set to 2 decimals
    update.values.amount = (
      Number(update.values.amount) - Number(subTotal)
    ).toFixed(2);

    if (update.values.goal) {
      update.values.percentage = Math.round(
        (update.values.amount / update.values.goal) * 100
      );
    }
    updateHistory(update, "-" + subTotal);
  };

  const scrollToRef = () => {
    window.scrollTo({
      top: ScrollRef.current.offsetTop,
      behavior: "smooth",
    });
  };

  const updateEnvelope = (e) => {
    let update = envelope;
    switch (e.name) {
      case "desc":
        update.desc = e.value;
        break;
      case "goal":
        update.values.goal = e.value;
        break;
      case "total":
        update.values.amount = e.value;
        break;
      case "tag":
        update.tag.name = e.value;
        break;
      case "tag-color":
        update.tag.color = e.value;
        break;
      default:
        return;
    }
    setEnvelope({ ...envelope, ...update });
  };

  const enableEdit = () => {
    toggleEditable(!editable);
    setEnvelopeCopy(_.cloneDeep(envelope));
  };

  const updateHistory = (update, amount) => {
    let date = new Date();
    date =
      date.getMonth() + 1 + "/" + date.getDate() + "/" + date.getFullYear();

    update.history.unshift({
      date: date,
      amount: amount,
      source: source,
    });

    console.log(update);

    setEnvelope({ ...envelope, ...update });
  };

  // Save envelope to db
  const save = async () => {
    // Save to db here
    toggleEditable(!editable);
  };

  const cancelSave = () => {
    setEnvelope({ ...envelopeCopy });
    toggleEditable(!editable);
  };

  return (
    <>
      <Nav />
      <section>
        {!_.isEmpty(envelope) && (
          <div className="container envelope">
            <h1>{envelope.name}</h1>
            <hr />
            <div className="flex-wrapper start mt-5">
              <div className="mr-auto fg-1">
                <CircleGraph props={envelope.values} showPercent={true} />
              </div>
              <form className="fg-3 mb-5">
                <div className="flex-wrapper">
                  {editable ? (
                    <h4 className="tag mb-4">
                      <span className={"badge bg-" + envelope.tag.color}>
                        {envelope.tag.name}
                      </span>
                    </h4>
                  ) : (
                    <div className="flex m-space">
                      <Input
                        value={envelope.tag.name}
                        onChange={(e) => updateEnvelope(e)}
                        className="mw-150"
                        label="Tag name"
                        name="tag"
                      />
                      <SelectColor
                        defaultValue={envelope.tag.color}
                        options={tagColors}
                        onChange={(e) => updateEnvelope(e)}
                        className="mw-150 mt-2"
                        name="tag-color"
                      />
                    </div>
                  )}
                  {editable ? (
                    <Button
                      className="btn-edit btn-light"
                      action={() => enableEdit()}
                    >
                      Edit envelope <span className="edit"></span>
                    </Button>
                  ) : (
                    <div className="flex m-space edit-mobile-wrapper">
                      <Button
                        className="btn-edit btn-success"
                        action={() => save()}
                      >
                        Save envelope
                      </Button>
                      <Button
                        className="btn-close mt-2"
                        title="Cancel"
                        action={() => cancelSave()}
                      />
                    </div>
                  )}
                </div>
                <h5>Description</h5>
                <Textarea
                  disabled={editable}
                  value={envelope.desc && envelope.desc}
                  placeholder={envelope.desc && envelope.desc}
                  name="desc"
                  onChange={(e) => updateEnvelope(e)}
                />
                <h5>Amounts</h5>
                <div className="flex-wrapper m-space">
                  <Input
                    disabled={editable}
                    value={envelope.values.goal && envelope.values.goal}
                    placeholder={
                      envelope.values.goal && "$" + envelope.values.goal
                    }
                    onChange={(e) => updateEnvelope(e)}
                    className="mw-150"
                    label="Goal"
                    name="goal"
                    help="Desired goal to reach."
                  />
                  <div className="flex column">
                    <AdjustableInput
                      disabled={editable}
                      value={envelope.values && envelope.values.amount}
                      placeholder={
                        envelope.values && "$" + envelope.values.amount
                      }
                      adjustableClick={(v) => adjuster(v)}
                      onChange={(e) => updateEnvelope(e)}
                      className="mw-150"
                      label="Total"
                      name="total"
                      help="Current amount in envelope."
                    />
                    <div ref={ScrollRef} className="input-button-wrapper">
                      {showPosAdjuster && (
                        <>
                          <Input
                            help="Enter amount to add."
                            className="mw-150"
                            name="add-total"
                            value={addTotal}
                            onChange={(e) => formatValues(e.value, "+")}
                          />
                          <Button
                            text="Add"
                            className="btn-success"
                            action={() => add2total()}
                          />
                          <Button
                            className="btn-close"
                            action={() => adjuster()}
                          />
                        </>
                      )}
                      {showNegAdjuster && (
                        // <div className="input-button-wrapper">
                        <>
                          <Input
                            type="number"
                            help="Enter amount to subtract."
                            className="mw-150"
                            name="sub-total"
                            value={subTotal}
                            onChange={(e) => formatValues(e.value, "-")}
                          />
                          <Button
                            text="Subtract"
                            className="btn-warning"
                            action={() => subFromTotal()}
                          />
                          <Button
                            className="btn-close"
                            action={() => adjuster()}
                          />
                        </>
                      )}
                    </div>
                  </div>
                  <div>
                    <Select
                      label="Source"
                      defaultValue="credit"
                      onChange={(e) => console.log(e)}
                      className="mw-150"
                      name="source"
                      options={[
                        { name: "Credit", value: "credit" },
                        { name: "Bank", value: "bank" },
                        { name: "Cash", value: "cash" },
                      ]}
                    />
                  </div>
                </div>
              </form>
            </div>
            {envelope.history && (
              <div>
                <h5>Transactions</h5>
                <Table props={envelope.history} />
              </div>
            )}
          </div>
        )}
      </section>
      <Footer />
    </>
  );
};

export default Envelopes;
