import React, { useState, useEffect, useCallback } from "react";
import Nav from "../common/nav";
import Footer from "../common/footer";
import Card from "../common/card";
import _ from "lodash";
import Button from "../common/Button";

const Home = (params) => {
  const [envelopes, setEnvelopes] = useState([
    {
      id: 1,
      name: "Emergency Savings",
      account: "HY Savings",
      desc: "Buffer fund",
      tag: { name: "Savings", color: "warning" },
      values: {
        goal: 12687,
        amount: 10724.73,
        percentage: 85,
      },
    },
    {
      id: 2,
      name: "Dining Out",
      account: "General Spending",
      desc: "Money for eating out",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 120,
      },
    },
    {
      id: 3,
      name: "Groceries",
      account: "General Spending",
      desc: "Money for grocery spending",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 280.19,
      },
    },
    {
      id: 4,
      name: "Kids",
      account: "General Spending",
      desc: "Kid related purchases",
      tag: { name: "Kids", color: "info" },
      values: {
        goal: null,
        amount: 30,
      },
    },
  ]);
  const [filtered, setFiltered] = useState(null);
  const [cards, setCards] = useState(null);
  const [filteredSummary, setFilteredSummary] = useState(null);

  const updateFilters = useCallback((values) => {
    renderEnvelopes(values);
  }, []);

  useEffect(() => {
    updateFilters(envelopes);
  }, [envelopes, updateFilters]);

  /**
   ** Filter envelopes based on tag name
   * @param {string} tag name of tag
   */
  const filterTags = (tag) => {
    const filter = Object.values(envelopes).filter(
      (values) => values.tag.name === tag
    );
    updateFilters(filter);
    filteredTotal(filter);
  };

  const filteredTotal = (object) => {
    let summary = {};
    Object.values(object).forEach((v) => {
      summary.tag = v.tag;
      summary.amount = summary.amount
        ? summary.amount + v.values.amount
        : v.values.amount;
    });
    setFilteredSummary({ ...summary });
  };

  const clearFiltered = () => {
    updateFilters(envelopes);
    setFilteredSummary(null);
  };

  /**
   ** Render cards with provided list
   * @param {object} object envelope object
   */
  const renderEnvelopes = (object) => {
    setCards(
      Object.values(object).map((values) => (
        <Card props={values} key={values.id} tagClick={(e) => filterTags(e)} />
      ))
    );
  };

  return (
    <>
      <Nav />
      <section>
        <div className="container">
          <h1>Envelopes</h1>
          <hr />
          {/* Sorted summary */}
          {filteredSummary && (
            <div className="flex-wrapper m-space mb-3">
              <div className={"info-tag badge bg-" + filteredSummary.tag.color}>
                {filteredSummary.tag.name}
              </div>
              <div className="info-tag badge total">
                Total ${filteredSummary.amount}
              </div>
              <Button
                className="btn-close info-tag badge"
                action={() => clearFiltered()}
              />
            </div>
          )}
          {/* Main wrapper */}
          <div className="flex-wrapper">{!_.isEmpty(cards) && cards}</div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Home;
