import { Link } from "@reach/router";
import React from "react";
import CircleGraph from "./circleGraph";
import Button from "./Button";

const Card = ({ props, tagClick }) => {
  return (
    <div className="card">
      <Button className="tag" action={() => tagClick(props.tag.name)}>
        <span className={"badge bg-" + props.tag.color}>{props.tag.name}</span>
      </Button>
      <CircleGraph props={props.values} />
      <div className="card-body">
        <h5 className="card-title">{props.name}</h5>
        <p className="card-text">{props.desc}</p>
        <Link to={"/envelope/" + props.id} className="btn btn-light float-end">
          Manage
        </Link>
      </div>
    </div>
  );
};
export default Card;
