import React from "react";

const Table = ({ props }) => {
  const renderProps = (props) => {
    return Object.values(props).map((v, i) => (
      <tr
        className={Number(v.amount) >= 0 ? "table-success" : "table-light"}
        key={i}
      >
        <td>{v.date}</td>
        <td>${v.amount}</td>
        <td>{v.source}</td>
      </tr>
    ));
  };

  return (
    <table className="table table-striped table-hover">
      <thead>
        <tr>
          <th scope="col">Date</th>
          <th scope="col">Amount</th>
          <th scope="col">Source</th>
        </tr>
      </thead>
      <tbody>{props && renderProps(props)}</tbody>
    </table>
  );
};
export default Table;
